// Fonts
$font1: 'SohoGothicPro', $font0;
$font2: $font1;

// Font Weights
$fw-normal: 400;
$fw-bold: 500;

// Font Sizes
$fs-energy-icon: 10px;

$font-sets: (
    body-xsmall: (
        font-size: 10,
        line-height: 12
    ),
    body-small: (
        font-size: 12,
        line-height: 12
    ),
    body-text: (
        font-size: 14,
        line-height: 18
    ),
    body-large: (
        font-size: 18,
        line-height: 24
    ),
    h-paragraph: (
        font-size: 18,
        line-height: 24,
        font-weight: $fw-bold
    ),
    body-xlarge: (
        font-size: 24,
        line-height: 32
    ),
    h-xtiny: (
        font-size: 12,
        line-height: 14,
        font-weight: $fw-bold
    ),
    h-tiny: (
        font-size: 14,
        line-height: 16,
        font-weight: $fw-bold
    ),
    h-xsmall: (
        font-size: 16,
        line-height: 20,
        font-weight: $fw-bold
    ),
    h-small: (
        font-size: 18,
        line-height: 22,
        font-weight: $fw-bold
    ),
    h: (
        font-size: 22,
        line-height: 26,
        font-weight: $fw-bold
    ),
    h-medium: (
        font-size: 32,
        line-height: 40,
        font-weight: $fw-bold
    ),
    h-large: (
        font-size: 36,
        line-height: 40,
        font-weight: $fw-bold
    )
);

// Line Height Base
$lh-base: map-get(map-get($font-sets, body-text), line-height)/map-get(map-get($font-sets, body-text), font-size);

// Border Radius
$br-bullet: 100%;
$br-popup-middle: 5px;
$br-popup-bottom: 10px;
$br-popover: 5px;
$br-product-card: 6px;
$br-support-box: 5px;

// Button
$btn-h-default: 44px;
$btn-h-small: 35px;
$btn-br-factor: .5;

// Form Elements
$h-input: 48px;
$h-textarea: 104px;

// Colours
