@mixin button-appshell {
    button{
        padding: 0;
        background: none;
        border: none;
        color: inherit;
        text-align: left;
    
        &:focus{
            outline: none;
        }
    }
}

@mixin d-elements-appshell {
    // desktop elements
    .d {
        display: none;
    }
}

@mixin qty-badge-appshell {
    .qty-badge {
        background: $c-important;
        color: $c-text-light;
        border-radius: 50%;
        width: 22px;
        line-height: 22px;
        font-size: 10px;
        text-align: center;
        &[data-qty="0"]{
            display: none;
        }
    }
}

@mixin header-appshell {
    // top header
    .header-top {
        position: fixed;
        top: 0;
        left: 0;
        z-index: $z-high;
        width: 100%;
        background: $header-top-bg;
        color: $header-top-color;
        transition: all .5s ease;
        > .container {
            max-width: calc(100% - #{$header-top-padding-x} * 2);
            > .row {
                border-bottom: $header-top-border-bottom;
                align-items: center;
                height: $header-height-mobile;
                > * {
                    display: flex;
                    align-items: center;
                }
                .col-top{
                    display: none;
                }
                .col-search{
                    display: none;
                    order: 1;
                }
            }
            .col-left-alt {
                display: none;
            }
            .col-center {
                justify-content: center;
            }
            .col-right {
                justify-content: flex-end;
            }
            .btn-menu{
                position: fixed;
                bottom: 20px;
                right: 20px;
                display: flex;
                width: 80px;
                height: 50px;
                justify-content: center;
                align-items: center;
                background-color: $c-white;
                box-shadow: 1px 1px 5px 0 rgba(0, 0, 0, 0.2);
                border-radius: 28.5px;
                @include transitions();
                .icon-menu{
                    color: $c-text-dark;
                }
            }
        }
    }
    .bottom-sticky-bar{
        .header-top > .container > .row .btn-menu{
            bottom: $bottom-sticky-bar-height + 20px;
        }
    }
    .v-kboard-opened{
        .header-top > .container > .row .btn-menu{
            display: none;
        }
    }
    
    .btn-notification {
        position: relative;
        .qty-badge {
            position: absolute;
            top: 0;
            right: -6px;
            width: 16px;
            height: 16px;
            line-height: 16px;
        }
    }

    .btn-cart{
        position: relative;
        .qty-badge {
            position: absolute;
            top: 0;
            right: -6px;
            width: 16px;
            height: 16px;
            line-height: 16px;
        }
    }
    
    .logo-alt {
        display: none;
    }
    
    .btn-cart {
        margin-left: 10px;
    }
}


// DESKTOP APPSHELL MIXINS
@mixin m-elements-appshell-desktop {
    // mobile elements
    .m {
        display: none !important;
    }
}

@mixin header-appshell-desktop {
    // top header
    .header-top {
        > .container {
            max-width: calc(100% - 20px * 2);
            > .row {
                height: $header-height-desktop;
                padding: 0 20px;
                > * {
                    > .d {
                        display: flex;
                        align-items: center;
                        > * {
                            margin-right: $d-header-item-space;
                        }
                    }
                }
                .col{
                    &-top{
                        display: flex;
                        justify-content: flex-end;
                        order: 1;
                        *{
                            margin-left: $d-header-item-space; 
                        }
                    }
                    &-search{
                        display: flex;
                        order: 4;
                        a{
                            width: 100%;
                            .f-input{
                                background-color: rgba($c-text-dark, .1);
                                padding: 5px 10px;
                            }
                        }
                    }
                    &-left{
                        order: 3;
                    }
                    &-right{
                        order: 5;
                    }
                    &-center{
                        order: 2;
                        justify-content: flex-start;
                    }
                }
                .btn-menu{
                    position: static;
                    display: inline;
                    background-color: transparent;
                    border-radius: 0;
                    box-shadow: none;
                }
            }
        }
    }

    .btn-menu {
        margin-right: $d-header-item-space;
        > .d {
            display: flex;
            align-items: center;
        }
    }
    .btn-cart {
        margin-left: 0;
    }
}
